import $ from 'jquery';
import _ from 'lodash';
import config from './config';

export const statSidebar = (data) => {
  // Cache the dom
  const $el = $('#gameStats');

  const $trainBars = $el.find('.transport__bars--train');
  const $bikeBars = $el.find('.transport__bars--bike');
  const $taxiBars = $el.find('.transport__bars--taxi');
  const $carBars = $el.find('.transport__bars--car');

  function calculateFastest() {
    // get kinetics from data
    const [car, train, bike] = data.kinetics;

    // we need the price later
    car.price = (car.distance / 1000) * config.prices.carKm;
    train.price = (train.distance / 1000) * config.prices.trainKm;

    // rank by fastest
    return _.orderBy([car, train, bike], ['duration'], ['asc']);
  }

  function setWidth() {
    const rankedTransport = calculateFastest();

    const fastestWidth = {
      width: 6,
      mode: rankedTransport[0].mode,
      price: rankedTransport[0].price,
    };
    const secondWidth = {
      width: (rankedTransport[0].duration / rankedTransport[1].duration) * fastestWidth.width,
      mode: rankedTransport[1].mode,
      price: rankedTransport[1].price,
    };
    const thirdWidth = {
      width: (rankedTransport[0].duration / rankedTransport[2].duration) * fastestWidth.width,
      mode: rankedTransport[2].mode,
      price: rankedTransport[2].price,
    };

    // Find the transport modes
    const DRIVING = _.find([fastestWidth, secondWidth, thirdWidth], { mode: 'DRIVING' });
    const BIKE = _.find([fastestWidth, secondWidth, thirdWidth], { mode: 'BICYCLING' });
    const TRAIN = _.find([fastestWidth, secondWidth, thirdWidth], { mode: 'TRANSIT' });

    // set time
    $trainBars.find('.transport__bars--time').css('width', `${TRAIN.width}vw`);
    $carBars.find('.transport__bars--time').css('width', `${DRIVING.width}vw`);
    $bikeBars.find('.transport__bars--time').css('width', `${BIKE.width}vw`);
    $taxiBars.find('.transport__bars--time').css('width', `${DRIVING.width * 0.95}vw`);

    // set price
    // find the max price
    const MOST_EXPENSIVE = _.maxBy(rankedTransport, 'price');

    $trainBars.find('.transport__bars--cost')
      .css('width', `${(TRAIN.price / MOST_EXPENSIVE.price) * 6}vw`);

    $carBars.find('.transport__bars--cost')
      .css('width', `${DRIVING.price / MOST_EXPENSIVE.price}vw`);

    $bikeBars.find('.transport__bars--cost')
      .css('width', `${0.2}vw`);

    $taxiBars.find('.transport__bars--cost')
      .css('width', `${6}vw`);

    // set comfort
    $trainBars.find('.transport__bars--comfort').css('width', `${6 * 0.85}vw`);
    $carBars.find('.transport__bars--comfort').css('width', `${6 * 0.97}vw`);
    $bikeBars.find('.transport__bars--comfort').css('width', `${6 * 0.3}vw`);
    $taxiBars.find('.transport__bars--comfort').css('width', `${6}vw`);
  }

  setWidth();
};
