'use strict';
import 'babel-polyfill';
import $ from 'jquery';
import 'jquery-ui/datepicker';
import event from './lib/pubsub.js';
import toastr from 'toastr';


let Direction = (() => {
  let _domElements = {};
  let _autoComplete;
  let distance = [];

  const _AIRPORT = {
    lat: 55.61802360000001,
    lng: 12.650762799999939
  };

  init();

  function init() {
    _cacheDom();
    _addressSelect();
    _initAutocomplete();
    _events();
  }

  function _cacheDom() {
    _domElements.address = document.getElementById('AutoComplete');
    _domElements.travelers = document.getElementById('calculator-travelers');
    _domElements.$date = $('#date');
    _domElements.calculate = document.getElementById('calculator-calculate');
  }

  function _events() {
    _domElements.calculate.addEventListener('click', calculateDistance);

  }

  function _initAutocomplete() {
    _autoComplete = new google.maps.places.Autocomplete(
      _domElements.address, {
        componentRestrictions: {
          country: 'dk'
        }
      });
  }

  // Get the address the user entered
  function _getEnteredAddress() {
    let _place = _autoComplete.getPlace();

    if (_place == null) {
      return 'error';
    } else {
      return {
        lat: _place.geometry.location.lat(),
        lng: _place.geometry.location.lng()
      };
    }
  }

  function calculateDistance() {
    let car   = distanceMatrix('DRIVING');
    if (car == null) return;

    let train = distanceMatrix('TRANSIT');
    let bike  = distanceMatrix('BIKE');

    let promise = new Promise((resolve, reject) => {
      Promise.all([car, train, bike]).then((res) => {
        resolve(res);

          // get all the input values
          let data = {
            kinetics: res,
            travelers: +_domElements.travelers.querySelector('input').value || 1,
            date: _domElements.$date.val() || new Date(),
          }
          // Notify subscribers
          event.emit('calculationDone', data);
        });
    });
    return promise;
  }

  function distanceMatrix(transportMode) {
    let address = _getEnteredAddress();
    
    if (address === 'error') {

      $(_domElements.address).focus();

      setTimeout(() => {
        $('#calculator').removeClass('spinner');
        $(_domElements.address).addClass('inputErrorClass');
        toastr.error('Vi kunne ikke finde den indtastede adresse, prøv igen');
      }, 1500)
      
      return;
    }

    let service = new google.maps.DistanceMatrixService();

    switch (transportMode) {
      case 'DRIVING':
      transportMode = google.maps.TravelMode.DRIVING;
      break;

      case 'BIKE':
      transportMode = google.maps.TravelMode.BICYCLING;
      break;

      case 'TRANSIT':
      transportMode = google.maps.TravelMode.TRANSIT;
      break;
    }

    let promise = new Promise((resolve, reject) => {
      service.getDistanceMatrix({
        origins: [new google.maps.LatLng(address.lat, address.lng)],
        destinations: [new google.maps.LatLng(_AIRPORT.lat, _AIRPORT.lng)],
        travelMode: transportMode,
        avoidHighways: false,
        avoidTolls: false,
      }, (response, stat) => {
        resolve({
          duration: response.rows[0].elements[0].duration.value,
          distance: response.rows[0].elements[0].distance.value,
          mode: transportMode,
          status: stat,
          unfiltered: response
        });
      });
    });

    return promise;
  }

  // Try to get the users current position
  function _getCurrentPosition() {
    let promise = new Promise((resolve, reject) => {
      if ('geolocation' in navigator) {
        navigator.geolocation.getCurrentPosition((position) => {
          this.geoLocation = position;
          resolve(position);
        }, (err) => reject(err));
      }
    });
    return promise;
  }

  function _addressSelect() {
    _domElements.$date.datepicker();

  }

  //calculateDistance returns a promise
  return {
    init,
    calculateDistance,
    _autoComplete
  };

});

export default Direction;
