import $ from 'jquery';
import gameState from './gameState.js';
import events from './lib/pubsub.js';
// All constants are in this file.
import { CAR_TURN, BIKE_TURN, TAXI_TURN, START_POS, BG_HEIGHT, BG_WIDTH } from './const';
import { getRandom } from './random';
import { statSidebar } from './lib/statSidebar';

export function canvas() {
  let
    car,
    train,
    bike,
    taxi,
    scale,
    clouds,
    speed,
    boat1,
    boat2,
    plane,
    traincover1,
    traincover2,
    traincover3,
    bridge,
    bridge2,
    airport,
    result,
    background,
    cursors,
    traincover4;

  const scrolling = { down: false, up: false };

  events.on('calculationDone', (response) => {
    speed = {
      car: 1000,
      taxi: (1000) * 0.95,
      bike: 1000 * (response.kinetics[0].duration / response.kinetics[2].duration),
      train: 1000 * (response.kinetics[0].duration / response.kinetics[1].duration),
    };

    statSidebar(response);
  });

  window.addEventListener('resize', () => {
    create();
  });

  const game = new Phaser.Game($(window).width(), $(window).height(), Phaser.CANVAS, 'app',
    { preload, create, update, render });

  function preload() {
    game.load.image('background', 'assets/images/game/background.jpg');
    game.load.image('car', 'assets/images/game/car.png');
    game.load.image('taxi', 'assets/images/game/taxi.png');
    game.load.image('bike', 'assets/images/game/bike.png');
    game.load.image('train', 'assets/images/game/train.png');
    game.load.image('traincover1', 'assets/images/game/traincover1.png');
    game.load.image('traincover2', 'assets/images/game/traincover2.png');
    game.load.image('traincover3', 'assets/images/game/traincover3.png');
    game.load.image('traincover4', 'assets/images/game/traincover4.png');
    game.load.image('cloud', 'assets/images/game/sky.png');
    game.load.image('result', 'assets/images/game/result.png');
    game.load.image('bridge', 'assets/images/game/bridge.png');
    game.load.image('airport', 'assets/images/game/airport.png');
    game.load.image('boat1', 'assets/images/game/boat1.png');
    game.load.image('boat2', 'assets/images/game/boat2.png');
    game.load.image('bridge2', 'assets/images/game/broad.png');
    game.load.image('plane', 'assets/images/game/plane.svg');
  }

  function create() {
    game.renderer.clearBeforeRender = false;
    game.renderer.roundPixels = true;

    game.physics.startSystem(Phaser.Physics.ARCADE);
    background = game.add.sprite(0, 0, 'background');

    scale = $(window).width() / background.width;
    game.world.setBounds(0, 0, BG_WIDTH * scale, BG_HEIGHT * scale - 500 * scale);

    // Add the sprites
    result = game.add.sprite(START_POS.result.x * scale, START_POS.result.y * scale, 'result');
    boat1 = game.add.sprite(START_POS.boat1.x * scale, START_POS.boat1.y * scale, 'boat1');
    boat2 = game.add.sprite(START_POS.boat2.x * scale, START_POS.boat2.y * scale, 'boat2');
    bridge2 = game.add.sprite(START_POS.bridge2.x * scale, START_POS.bridge2.y * scale, 'bridge2');
    train = game.add.sprite(START_POS.train.x * scale, START_POS.train.y * scale, 'train');
    traincover1 =
      game.add.sprite(
        START_POS.traincover1.x * scale, START_POS.traincover1.y * scale, 'traincover1'
      );
    traincover2 =
      game.add.sprite(
        START_POS.traincover2.x * scale, START_POS.traincover2.y * scale, 'traincover2'
      );
    traincover3 =
      game.add.sprite(
        START_POS.traincover3.x * scale, START_POS.traincover3.y * scale, 'traincover3'
      );
    traincover4 =
      game.add.sprite(
        START_POS.traincover4.x * scale, START_POS.traincover4.y * scale, 'traincover4'
      );
    bike = game.add.sprite(START_POS.bike.x * scale, START_POS.bike.y * scale, 'bike');
    car = game.add.sprite(START_POS.car.x * scale, START_POS.car.y * scale, 'car');
    taxi = game.add.sprite(START_POS.taxi.x * scale, START_POS.taxi.y * scale, 'taxi');
    bridge = game.add.sprite(START_POS.bridge.x * scale, START_POS.bridge.y * scale, 'bridge');
    airport = game.add.sprite(START_POS.airport.x * scale, START_POS.airport.y * scale, 'airport');
    plane = game.add.sprite(START_POS.plane.x * scale, START_POS.plane.y * scale, 'plane');

    result.scale.setTo(scale * 2);
    result.anchor.set(0.5);
    result.inputEnabled = true;

    function listener() {
      gameState.showResults();
    }

    //  Enable the hand cursor
    result.input.useHandCursor = true;
    result.events.onInputDown.add(listener, this);

    // Add the clouds
    clouds = [];
    for (let i = 0; i < 50 * getRandom(0.2, 1); i++) {
      clouds.push(
        game.add.sprite(
          BG_WIDTH * scale * Math.random(), BG_HEIGHT * scale * Math.random(), 'cloud'
        ));
      clouds[i].scale.setTo(getRandom(0.2, 0.9) * scale);
      game.physics.arcade.enable(clouds[i], Phaser.Physics.ARCADE);
    }

    // Enable the boats physics
    game.physics.arcade.enable(boat1, Phaser.Physics.ARCADE);
    game.physics.arcade.enable(boat2, Phaser.Physics.ARCADE);
    game.physics.arcade.enable(plane, Phaser.Physics.ARCADE);

    const spritesArr = [car, train, bike, taxi, bridge, background, airport,
                    boat1, boat2, bridge2, plane,
                    traincover1, traincover2, traincover3, traincover4];

    // configure the sprites
    for (const prop in spritesArr) {
      if ({}.hasOwnProperty.call(spritesArr, prop)) {
        spritesArr[prop].scale.setTo(scale);
        if (prop < 4) {
          spritesArr[prop].anchor.set(0.5);
          game.physics.arcade.enable(spritesArr[prop], Phaser.Physics.ARCADE);

          spritesArr[prop].body.maxVelocity.set(2000, 2000);
          spritesArr[prop].body.drag.set(1500);
        }
      }
    }

    cursors = game.input.keyboard.createCursorKeys();
    game.camera.follow(car);

    events.on('playState', () => {
      document.addEventListener('touchmove', () => {
        scrolling.down = true;
      }, false);
    });

    function mouseWheel() {
      switch (game.input.mouse.wheelDelta) {
        case 1:
          scrolling.up = true;
          scrolling.down = false;
          break;

        case -1:
          scrolling.up = false;
          scrolling.down = true;
          break;

        default:
          break;
      }
    }

    game.input.mouse.mouseWheelCallback = mouseWheel;
  }

  function update() {
    // game.physics.arcade.collide(car, train);
    // game.physics.arcade.collide(car, bike);
    // game.physics.arcade.collide(car, taxi);

    // ANiMATE CLOUDS
    for (let i = 0; i < clouds.length; i++) {
      clouds[i].body.velocity.x = 20 * scale;
      game.world.wrap(clouds[i], 0, true);
    }

    // Animate boats
    boat1.body.velocity.x = -50 * scale;
    boat2.body.velocity.x = 120 * scale;
    game.world.wrap(boat1, 0, true);
    game.world.wrap(boat2, 0, true);
    game.world.wrap(plane, 0, true);

    // Animate plane
    plane.body.velocity.x = 500 * scale;

    /* =================================== */
    /*            CAR MOVEMENT             */
    /* =================================== */
    const carWithinFirst =
      (car.position.y >= CAR_TURN.first.y * scale) &&
      (car.position.y <= CAR_TURN.first.yTo * scale);

    const carWithinSecond =
      (car.position.y >= CAR_TURN.second.y * scale) &&
      (car.position.y <= CAR_TURN.second.yTo * scale);

    const carWithinThird =
      (car.position.y >= CAR_TURN.third.y * scale) &&
      (car.position.y <= CAR_TURN.third.yTo * scale);

    if (scrolling.down) { // FIRST MOVEMENT POINT
      if (carWithinFirst) {
        const rotation = game.physics.arcade.angleToXY(
          car, CAR_TURN.first.xTo * scale, CAR_TURN.first.yTo * scale
        );
        const newRot = (rotation - 1.57) * (180 / Math.PI);
        game.add.tween(car).to({ angle: newRot }, 80, Phaser.Easing.Linear.In, true, -1);
        game.physics.arcade.velocityFromRotation(rotation, speed.car * scale, car.body.velocity);
      } else if (carWithinSecond) {
        const rotation = game.physics.arcade.angleToXY(
          car, CAR_TURN.second.xTo * scale, CAR_TURN.second.yTo * scale
        );
        const newRot = (rotation - 1.57) * (180 / Math.PI);
        game.add.tween(car).to({ angle: newRot }, 80, Phaser.Easing.Linear.In, true, -1);
        game.physics.arcade.velocityFromRotation(rotation, speed.car * scale, car.body.velocity);
      } else if (carWithinThird) {
        const rotation = game.physics.arcade.angleToXY(
          car, CAR_TURN.third.xTo * scale, CAR_TURN.third.yTo * scale
        );
        const newRot = (rotation - 1.57) * (180 / Math.PI);
        game.add.tween(car).to({ angle: newRot }, 80, Phaser.Easing.Linear.In, true, -1);
        game.physics.arcade.velocityFromRotation(rotation, speed.car * scale, car.body.velocity);
      } else {
        car.body.velocity.y = speed.car * scale;
        game.add.tween(car).to({angle: 0}, 80, Phaser.Easing.Linear.In, true, -1);
      }
    }

  /* =====  End of CAR MOVEMENT  ====== */

  /* ===================================== */
  /*             BIKE MOVEMENT             */
  /* ===================================== */

    const bikeWithinFirst =
      (bike.position.y >= BIKE_TURN.first.y * scale) &&
      (bike.position.y <= BIKE_TURN.first.yTo * scale);

    const bikeWithinSecond =
      (bike.position.y >= BIKE_TURN.second.y * scale) &&
      (bike.position.y <= BIKE_TURN.second.yTo * scale);

    const bikeWithinThird =
      (bike.position.y >= BIKE_TURN.third.y * scale) &&
      (bike.position.y <= BIKE_TURN.third.yTo * scale);

    if (scrolling.down) {
       // FIRST MOVEMENT POINT
      if (bikeWithinFirst) {
        const rotation =
          game.physics.arcade.angleToXY(
            bike, BIKE_TURN.first.xTo * scale, BIKE_TURN.first.yTo * scale
          );

        bike.rotation = rotation - 1.57;
        game.physics.arcade.velocityFromRotation(rotation, speed.bike * scale, bike.body.velocity);
      } else {
        bike.body.velocity.y = speed.bike * scale;
        bike.rotation = 0;
      }

      // Second movement Point
      if (bikeWithinSecond) {
        const rotation =
          game.physics.arcade.angleToXY(
            bike, BIKE_TURN.second.xTo * scale, BIKE_TURN.second.yTo * scale
          );

        bike.rotation = rotation - 1.57;
        game.physics.arcade.velocityFromRotation(rotation, speed.bike * scale, bike.body.velocity);
      }

      // Third movement Point
      if (bikeWithinThird) {
        const rotation =
          game.physics.arcade.angleToXY(
            bike, BIKE_TURN.third.xTo * scale, BIKE_TURN.third.yTo * scale
          );

        bike.rotation = rotation - 1.57;
        game.physics.arcade.velocityFromRotation(rotation, speed.bike * scale, bike.body.velocity);
      }
    }

  /* ====  End of BIKE MOVEMENT  ===== */


  /* ================================= */
  /*          TAXI MOVEMENT            */
  /* ================================= */

    const taxiWithinFirst =
      (taxi.position.y >= TAXI_TURN.first.y   * scale) &&
      (taxi.position.y <= TAXI_TURN.first.yTo * scale);

    const taxiWithinSecond =
      (taxi.position.y >= TAXI_TURN.second.y * scale) &&
      (taxi.position.y <= TAXI_TURN.second.yTo * scale);

    const taxiWithinThird =
      (taxi.position.y >= TAXI_TURN.third.y * scale) &&
      (taxi.position.y <= TAXI_TURN.third.yTo * scale);

    if (scrolling.down) {
       // FIRST MOVEMENT POINT
      if (taxiWithinFirst) {
        const rotation =
          game.physics.arcade.angleToXY(
            taxi, TAXI_TURN.first.xTo * scale, TAXI_TURN.first.yTo * scale
          );
        const newRot = (rotation - 1.57) * (180 / Math.PI);
        game.add.tween(taxi).to({ angle: newRot }, 80, Phaser.Easing.Linear.In, true, -1);
        game.physics.arcade.velocityFromRotation(rotation, speed.taxi * scale, taxi.body.velocity);
      } else if (taxiWithinSecond) {
        const rotation =
          game.physics.arcade.angleToXY(
            taxi, TAXI_TURN.second.xTo * scale, TAXI_TURN.second.yTo * scale
          );

        const newRot = (rotation - 1.57) * (180 / Math.PI);
        game.add.tween(taxi).to({ angle: newRot }, 80, Phaser.Easing.Linear.In, true, -1);
        game.physics.arcade.velocityFromRotation(rotation, speed.taxi * scale, taxi.body.velocity);
      } else if (taxiWithinThird) {
        const rotation =
          game.physics.arcade.angleToXY(
            taxi, TAXI_TURN.third.xTo * scale, TAXI_TURN.third.yTo * scale
          );
        const newRot = (rotation - 1.57) * (180 / Math.PI);
        game.add.tween(taxi).to({ angle: newRot }, 80, Phaser.Easing.Linear.In, true, -1);
        game.physics.arcade.velocityFromRotation(rotation, speed.taxi * scale, taxi.body.velocity);
      } else {
        taxi.body.velocity.y = speed.taxi * scale;
        game.add.tween(taxi).to({ angle: 0 }, 80, Phaser.Easing.Linear.In, true, -1);
      }
    }

    /* =================================== */
    /*           train MOVEMENT            */
    /* =================================== */
    if (scrolling.down) {
      train.body.velocity.y = speed.train * scale;
      if (train.position.y > 4800 * scale && train.position.y < 7500 * scale) {
        train.body.velocity.y = speed.train * scale * 0.77;
      } else if (train.position.y > 7500 * scale && train.position.y < 10300 * scale) {
        train.body.velocity.y = speed.train * scale;
      }
    }

    if (train.position.y > 1800 * scale && train.position.x < 1000 * scale) {
      train.position.x = 1330 * scale;
      train.position.y = 1500 * scale;
    }

    if (train.position.y > 4800 * scale && train.position.y < 7500 * scale) {
      train.alpha = 0;
      train.position.x = 1555 * scale;
    } else {
      train.alpha = 1;
    }

    scrolling.down = false;
    scrolling.up = false;


    /* AT THE END HIDE THE VEHICLES */

    if (car.position.y > START_POS.airport.y * scale + (200 * scale)) {
      car.alpha = 0;
    }
    if (taxi.position.y > START_POS.airport.y * scale + (200 * scale)) {
      taxi.alpha = 0;
    }
    if (bike.position.y > START_POS.airport.y * scale + (200 * scale)) {
      bike.alpha = 0;
    }

    if (train.position.y > 10300 * scale) {
      train.body.velocity.y = 0;
    }
  }

  function render() {
    // game.debug.spriteCoords(train, 32, 500);
    //
    // // game.debug.body(car);
    // game.debug.body(train);
    // game.debug.spriteInfo(car, 30, 30);
  }
}
