import event from './pubsub.js';
import $ from 'jquery';

export default (() => {
  var lastScrollTop = 0;
  $(window).scroll(function(e){
    var st = $(this).scrollTop();
    // console.log(st);
    if (st > lastScrollTop){
      e.direction = -1;
      event.emit('scrollDirection', e);
    } else {
      e.direction = 1;
      event.emit('scrollDirection', e);
    }
    lastScrollTop = st;
  });
})();
