'use strict';
import $ from 'jquery';
import 'babel-polyfill';
import events from './lib/pubsub.js';
import config from './lib/config.js';
import { animateThis } from './lib/animateThis';

const results = (() => {
  // cache the dom
  const $el = $('#results-box');

  const $carPrice = $el.find('#results-box-car .progress__price--text span');
  const $bikePrice = $el.find('#results-box-bike .progress__price--text span');
  const $taxiPrice = $el.find('#results-box-taxi .progress__price--text span');
  const $trainPrice = $el.find('#results-box-train .progress__price--text span');
  const $carTime = $el.find('#results-box-car .progress__time--text span');
  const $bikeTime = $el.find('#results-box-bike .progress__time--text span');
  const $taxiTime = $el.find('#results-box-taxi .progress__time--text span');
  const $trainTime = $el.find('#results-box-train .progress__time--text span');
  const $carJoy = $el.find('#results-box-car .progress__joyment--text span');
  const $bikeJoy = $el.find('#results-box-bike .progress__joyment--text span');
  const $taxiJoy = $el.find('#results-box-taxi .progress__joyment--text span');
  const $trainJoy = $el.find('#results-box-train .progress__joyment--text span');

  // global vars
  let
  travelers,
  date,
  prices,
  time,
  originAddress;

  events.on('calculationDone', (data) => {
    calculateStats(data);
  });

  events.on('showResults', scrollToResult);

  function calculateStats(data) {
    travelers = data.travelers;
    date = data.date;
    originAddress = data.kinetics[0].unfiltered.originAddresses[0];

    const car = data.kinetics[0];
    const train = data.kinetics[1];
    const bike = data.kinetics[2];

    const carPrice = +(car.distance / 1000) * config.prices.carKm + addBridge(originAddress);
    const trainPrice = +((train.distance / 1000) * config.prices.trainKm) * travelers;
    const taxiPrice = +(car.distance / 1000) * config.prices.taxiKm;

    prices = {
      carPrice,
      trainPrice,
      taxiPrice,
    };

    time = {
      car,
      train,
      bike,
    };
  }

  function addBridge(address) {
    const zipCode = +address.match(/\s(\d{4})\s/g)[0];
    if (zipCode >= 5000) {
      return 220;
    }
    return 0;
  }

  function scrollToResult() {
    // scroll to the results section
    $('html, body').animate({
      scrollTop: $('#results').offset().top
    }, 1000);

    // when the animation is finsihed
    // then animate the bars
    setTimeout(() => {
      animateBars();
    }, 1250);
  }

  function animateBars() {
    let relativeTime;

    if (time.car.duration > time.train.duration && time.car.duration > time.bike.duration) {
      // car is slowest
      relativeTime = {
        car: '100%',
        bike: `${(time.bike.duration / time.car.duration) * 100}%`,
        train: `${(time.train.duration / time.car.duration) * 100}%`,
      };
    }

    if (time.bike.duration > time.car.duration && time.bike.duration > time.train.duration) {
      // bike is slowest
      relativeTime = {
        bike: '100%',
        car: `${(time.car.duration / time.bike.duration) * 100}%`,
        train: `${(time.train.duration / time.bike.duration) * 100}%`,
      };
    }

    if (time.train.duration > time.car.duration && time.train.duration > time.bike.duration) {
      // train is slowest
      relativeTime = {
        train: '100%',
        bike: `${(time.bike.duration / time.train.duration) * 100}%`,
        car: `${(time.car.duration / time.train.duration) * 100}%`,
      };
    }

    animateThis('#stats-car', relativeTime.car, '.time');
    animateThis('#stats-car', '97%', '.comfort');
    animateThis('#stats-car', '60%', '.price');
    animateThis('#stats-bike', relativeTime.bike, '.time');
    animateThis('#stats-bike', '20%',	'.comfort');
    animateThis('#stats-bike', '1%', '.price');
    animateThis('#stats-train', relativeTime.train, 	'.time');
    animateThis('#stats-train', '85%', '.comfort');
    animateThis('#stats-train', '60%', '.price');
    animateThis('#stats-taxi', relativeTime.car, '.time');
    animateThis('#stats-taxi', '100%',	'.comfort');
    animateThis('#stats-taxi', '100%',	'.price');

    appendResults();
  }

  function appendResults() {
    // Append the calculated prices
    $($carPrice).html(`${prices.carPrice.toFixed(0)} Kr.`);
    $($bikePrice).html(0 + ' Kr.');
    $($trainPrice).html(`${prices.trainPrice.toFixed(0)} Kr.`);
    $($taxiPrice).html(`${prices.taxiPrice.toFixed(0)} Kr.`);

    // Append the calculated time
    $($carTime).html(`${(time.car.duration 		/ 60).toFixed(0)}	min.`);
    $($bikeTime).html(`${(time.bike.duration 	/ 60).toFixed(0)} min.`);
    $($trainTime).html(`${(time.train.duration / 60).toFixed(0)} min.`);
    $($taxiTime).html(`${+(time.car.duration 		/ 60).toFixed(0) + 10} min.`);

    // Append the calculated joyment
    const { comfort } = config;
    const { trainPrice, carPrice, taxiPrice } = prices;
    const { train: trainT, bike: bikeT, car: carT } = time;

    const CAR_JOY = ((comfort.car * 100) - carPrice - (carT.duration / 60)) / 100;
    const TRAIN_JOY = ((comfort.train * 100) - trainPrice - (trainT.duration / 60)) / 100;
    const TAXI_JOY = ((comfort.taxi * 100) - taxiPrice - (carT.duration / 60)) / 100;
    const BIKE_JOY = ((comfort.bike * 100) - 0 - (bikeT.duration / 60)) / 100;

    $($carJoy).html(CAR_JOY.toFixed(0));
    $($trainJoy).html(TRAIN_JOY.toFixed(0));
    $($taxiJoy).html(TAXI_JOY.toFixed(0));
    $($bikeJoy).html(BIKE_JOY.toFixed(0));

    addDirectionListners();
  }

  function addDirectionListners() {
    $('.show-route').on('click', function showMap(event) {
      event.preventDefault();

      $('#map').removeClass('hidden');
      $('#close-button').removeClass('hidden');


      initMap($(this).data('trans'));
    });

    $('#close-button').on('click', () => {
      $('#map').addClass('hidden');
      $('#close-button').addClass('hidden');
    });
  }

  // Takes the transport mode as param
  function initMap(mode) {
    // Init the map variables
    let directionsService = new google.maps.DirectionsService;
    let directionsDisplay = new google.maps.DirectionsRenderer;

    // set the map center
    const map = new google.maps.Map(document.getElementById('map'), {
      zoom: 7,
      center: { lat: 56.263920, lng: 9.501785 }
    });

    directionsDisplay.setMap(map);

    // Get directions
    calculateAndDisplayRoute(directionsService, directionsDisplay, mode);
  }

  function calculateAndDisplayRoute(directionsService, directionsDisplay, mode) {

      if (mode === 'BIKE') mode = 'BICYCLING';
      directionsService.route({
        origin: originAddress,
        destination: 'Kastrup, Denmark',
        travelMode: google.maps.TravelMode[mode]
      }, (response, status) => {
        if (status === google.maps.DirectionsStatus.OK) {

          directionsDisplay.setDirections(response);

        } else {

        }
    });

  }
})();

export default results;
