import { canvas } from './canvas.js';
import $ from 'jquery';
import events from './lib/pubsub.js';
import distance from './main.js';

let gameState = gameState || {};


export default gameState = {
	top () {
		header.classList.remove('hidden');
		app.classList.add('hidden');
		bottom.classList.add('hidden');
	},

	playGame () {
		document.addEventListener('mousewheel', this.preventDefaultFunc)
    gameStats.classList.remove('hidden');
		app.classList.remove('hidden');
		header.classList.add('hidden');
		bottom.classList.add('hidden');
		$(document).scrollTop(0);
		events.emit('playState', true);
	},

	showResults () {
		document.removeEventListener('mousewheel', this.preventDefaultFunc)
		header.classList.add('hidden');
    gameStats.classList.add('hidden');
		app.classList.add('hidden');
		bottom.classList.remove('hidden');

		events.emit('showResults', []);
	},

	preventDefaultFunc (e) {
		e.preventDefault();
	}
};
